// SensorCards.jsx
import React, { useEffect, useState } from 'react';
import { ArrowUp, ArrowDown, Minus, Droplet, Zap, Activity, Database } from 'lucide-react';

// Units for each metric type
const UNITS = {
  wavelength: 'nm',
  power: 'dBm',
  strain: 'µε',
  temperature: '°C'
};

// Decimal precision for each metric type
const PRECISION = {
  wavelength: 3,
  power: 2,
  strain: 8,
  temperature: 1
};

// Thresholds for trend detection
const THRESHOLDS = {
  wavelength: 0.0005,  // 0.5 pm
  power: 0.05,         // 0.05 dBm
  strain: 0.1,         // 0.1 µε
  temperature: 0.05    // 0.05 °C
};

// Get icon for each metric type
const getMetricIcon = (tab, size = 16) => {
  switch(tab) {
    case 'wavelength':
      return <Droplet size={size} />;
    case 'power':
      return <Zap size={size} />;
    case 'strain':
      return <Activity size={size} />;
    case 'temperature':
      return <Droplet size={size} className="text-red-500" />;
    default:
      return <Database size={size} />;
  }
};

/**
 * Component to display individual sensor cards with live data
 */
const SensorCards = ({ selectedTab, currentValues, previousValues }) => {
  // State to track trend directions for animation
  const [trends, setTrends] = useState({});
  
  // Process incoming values based on selected tab
  useEffect(() => {
    if (!currentValues || !previousValues) return;
    
    const newTrends = {};
    const threshold = THRESHOLDS[selectedTab] || 0.001;
    
    // Different processing based on data structure
    if (selectedTab === 'wavelength' || selectedTab === 'power') {
      // These metrics have channel/sensor structure
      currentValues.forEach((channelData, channelIdx) => {
        channelData?.forEach((value, sensorIdx) => {
          const key = `CH${channelIdx}S${sensorIdx}`;
          const previousKey = `${selectedTab}_${key}`;
          const previousValue = previousValues[previousKey];
          
          if (previousValue !== undefined) {
            // Ensure we're working with numbers
            const currentNum = typeof value === 'number' ? value : parseFloat(value);
            let prevNum;
            
            if (typeof previousValue === 'string' && previousValue.includes(' ')) {
              // Extract numeric value from previous value (format: "1234.56 nm")
              prevNum = parseFloat(previousValue.split(' ')[0]);
            } else if (typeof previousValue === 'string') {
              prevNum = parseFloat(previousValue);
            } else {
              prevNum = previousValue;
            }
            
            if (!isNaN(currentNum) && !isNaN(prevNum)) {
              if (currentNum > prevNum + threshold) {
                newTrends[key] = 'up';
              } else if (currentNum < prevNum - threshold) {
                newTrends[key] = 'down';
              } else {
                newTrends[key] = 'stable';
              }
            } else {
              newTrends[key] = 'stable';
            }
          } else {
            newTrends[key] = 'stable';
          }
        });
      });
    } else {
      // Strain and temperature have flat sensor structure
      currentValues?.forEach((value, sensorIdx) => {
        const key = `S${sensorIdx + 1}`;
        const previousKey = `${selectedTab}_${key}`;
        const previousValue = previousValues[previousKey];
        
        if (previousValue !== undefined) {
          // Ensure we're working with numbers
          const currentNum = typeof value === 'number' ? value : parseFloat(value);
          let prevNum;
          
          if (typeof previousValue === 'string' && previousValue.includes(' ')) {
            // Extract numeric value from previous value (format: "1234.56 °C")
            prevNum = parseFloat(previousValue.split(' ')[0]);
          } else if (typeof previousValue === 'string') {
            prevNum = parseFloat(previousValue);
          } else {
            prevNum = previousValue;
          }
          
          if (!isNaN(currentNum) && !isNaN(prevNum)) {
            if (currentNum > prevNum + threshold) {
              newTrends[key] = 'up';
            } else if (currentNum < prevNum - threshold) {
              newTrends[key] = 'down';
            } else {
              newTrends[key] = 'stable';
            }
          } else {
            newTrends[key] = 'stable';
          }
        } else {
          newTrends[key] = 'stable';
        }
      });
    }
    
    // Only update trends if there's actual change to prevent component re-renders
    if (JSON.stringify(trends) !== JSON.stringify(newTrends)) {
      setTrends(newTrends);
    }
  }, [currentValues, previousValues, selectedTab]);
  
  // Helper function to format a value with the correct precision and unit
  const formatValue = (value, tab = selectedTab) => {
    if (value === undefined || value === null) return "--";
    // Ensure we're dealing with a number
    const numericValue = typeof value === 'number' ? value : parseFloat(value);
    if (isNaN(numericValue)) return "--";
    
    return `${numericValue.toFixed(PRECISION[tab] || 2)} ${UNITS[tab] || ''}`;
  };
  
  // Render the appropriate sensor card layout based on the selected tab
  const renderSensorCards = () => {
    if (!currentValues) {
      return <div className="text-gray-500">No data available</div>;
    }
    
    if (selectedTab === 'wavelength' || selectedTab === 'power') {
      // Render cards for channel/sensor structure (wavelength, power)
      return (
        <div className="flex flex-col space-y-4">
          {/* Channel 0 Section */}
          <div className="bg-white p-3 rounded-lg shadow">
            <div className="flex items-center mb-2">
              <Database size={16} className="text-indigo-600 mr-1.5" />
              <h3 className="text-base font-medium text-gray-700">Channel 0</h3>
            </div>
            <div className="flex flex-wrap gap-2">
              {currentValues[0]?.map((value, sensorIdx) => (
                <div className="flex-1 min-w-fit" key={`CH0S${sensorIdx}`}>
                  <SensorCard 
                    label={`CH0S${sensorIdx + 1}`}
                    value={formatValue(value)}
                    trend={trends[`CH0S${sensorIdx}`]}
                    color={`hsl(${sensorIdx * 30}, 70%, 50%)`}
                    icon={getMetricIcon(selectedTab, 14)}
                  />
                </div>
              ))}
            </div>
          </div>
          
          {/* Channel 1 Section */}
          <div className="bg-white p-3 rounded-lg shadow">
            <div className="flex items-center mb-2">
              <Database size={16} className="text-indigo-600 mr-1.5" />
              <h3 className="text-base font-medium text-gray-700">Channel 1</h3>
            </div>
            <div className="flex flex-wrap gap-2">
              {currentValues[1]?.map((value, sensorIdx) => (
                <div className="flex-1 min-w-fit" key={`CH1S${sensorIdx}`}>
                  <SensorCard 
                    label={`CH1S${sensorIdx + 1}`}
                    value={formatValue(value)}
                    trend={trends[`CH1S${sensorIdx}`]}
                    color={`hsl(${(sensorIdx + 5) * 30}, 70%, 50%)`}
                    icon={getMetricIcon(selectedTab, 14)}
                  />
                </div>
              ))}
            </div>
          </div>
        </div>
      );
    } else {
      // Render cards for flat sensor structure (strain, temperature)
      return (
        <div className="bg-white p-4 rounded-lg shadow">
          <div className="flex items-center mb-2">
            {getMetricIcon(selectedTab, 18)}
            <h3 className="text-base font-medium ml-1.5 text-gray-700">
              {selectedTab.charAt(0).toUpperCase() + selectedTab.slice(1)} Sensors
            </h3>
          </div>
          <div className="flex flex-wrap gap-3">
            {currentValues.map((value, idx) => (
              <div className="flex-1 min-w-fit" key={`S${idx + 1}`}>
                <SensorCard 
                  label={`S${idx + 1}`}
                  value={formatValue(value)}
                  trend={trends[`S${idx + 1}`]}
                  color={`hsl(${idx * 50}, 70%, 50%)`}
                  icon={getMetricIcon(selectedTab, 14)}
                />
              </div>
            ))}
          </div>
        </div>
      );
    }
  };
  
  return (
    <div className="p-1">
      {renderSensorCards()}
    </div>
  );
};

/**
 * Individual sensor card component
 */
const SensorCard = React.memo(({ label, value, trend, color, icon }) => {
  // Style based on trend
  const getTrendStyle = () => {
    switch (trend) {
      case 'up':
        return {
          color: '#28a745',
          backgroundColor: 'rgba(40, 167, 69, 0.1)',
          icon: <ArrowUp size={14} className="ml-1" />
        };
      case 'down':
        return {
          color: '#dc3545',
          backgroundColor: 'rgba(220, 53, 69, 0.1)',
          icon: <ArrowDown size={14} className="ml-1" />
        };
      default:
        return {
          color: '#6c757d',
          backgroundColor: 'white',
          icon: <Minus size={14} className="ml-1" />
        };
    }
  };
  
  const trendStyle = getTrendStyle();
  
  return (
    <div 
      className="rounded-lg p-2 transition-all duration-500 ease-in-out"
      style={{ 
        border: `2px solid ${color}`,
        backgroundColor: trendStyle.backgroundColor
      }}
    >
      <div className="flex items-center text-xs font-semibold text-gray-600 mb-1">
        <span className="mr-1">{icon}</span>
        {label}
      </div>
      <div className="flex items-center justify-between">
        <span 
          className="text-sm font-bold"
          style={{ color: trendStyle.color }}
        >
          {value}
        </span>
        <span style={{ color: trendStyle.color }}>
          {trendStyle.icon}
        </span>
      </div>
    </div>
  );
});

export default SensorCards;