// Dashboard.jsx
import React, { useState, useEffect, useRef } from 'react';
import { useQuery } from 'react-query';
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer
} from 'recharts';
import { Activity, Thermometer, Waves, Zap, Server } from 'lucide-react';
import SensorCards from './SensorCards';

// API Configuration - accept connections universally without checking environment
const getApiBaseUrl = () => {
  // Check for different host environments
  const hostname = window.location.hostname;
  
  if (hostname === 'localhost') {
    return 'http://localhost:8002';
  } else if (hostname.includes('pythonanywhere.com')) {
    return `https://${hostname}`;
  } else if (hostname.includes('masterfilmon.com')) {
    return 'https://api.masterfilmon.com';
  } else {
    // Fallback to current origin
    return window.location.origin;
  }
};

// WebSocket connection for real-time updates
function useWebSocket(url) {
  const [data, setData] = useState(null);
  const [lastMessage, setLastMessage] = useState(null);
  const [error, setError] = useState(null);
  const [connectionStatus, setConnectionStatus] = useState('disconnected');
  const socket = useRef(null);
  const reconnectAttempts = useRef(0);
  const maxReconnectAttempts = 5;
  
  useEffect(() => {
    const connect = () => {
      try {
        console.log(`Attempting WebSocket connection to ${url}`);
        
        // More detailed connection debugging
        console.log(`Current protocol: ${window.location.protocol}`);
        console.log(`Socket protocol: ${url.startsWith('wss') ? 'wss' : 'ws'}`);
        
        setConnectionStatus('connecting');
        socket.current = new WebSocket(url);
        
        socket.current.onopen = () => {
          console.log('WebSocket connected successfully');
          setConnectionStatus('connected');
          reconnectAttempts.current = 0;
          
          // Send a ping immediately to test bidirectional communication
          socket.current.send(JSON.stringify({type: 'ping', timestamp: new Date().toISOString()}));
        };
        
        socket.current.onmessage = (event) => {
          try {
            setLastMessage(event.data); // Store raw message for debugging
            const parsedData = JSON.parse(event.data);
            console.log('Received data from server:', parsedData);
            setData(parsedData);
          } catch (err) {
            console.error('Error parsing WebSocket data:', err, event.data);
          }
        };
        
        // Enhanced error handling
        socket.current.onerror = (err) => {
          console.error('WebSocket error:', err);
          setError(err);
          setConnectionStatus('error');
        };
        
        socket.current.onclose = (event) => {
          console.log(`WebSocket closed with code ${event.code}, reason: ${event.reason || 'No reason provided'}`);
          setConnectionStatus('disconnected');
          
          if (reconnectAttempts.current < maxReconnectAttempts) {
            const delay = Math.min(1000 * Math.pow(2, reconnectAttempts.current), 10000);
            reconnectAttempts.current += 1;
            console.log(`Reconnecting in ${delay}ms... (Attempt ${reconnectAttempts.current}/${maxReconnectAttempts})`);
            
            setTimeout(connect, delay);
          } else {
            console.error('Maximum reconnection attempts reached');
            setConnectionStatus('failed');
          }
        };
      } catch (e) {
        console.error('Error creating WebSocket connection:', e);
        setConnectionStatus('error');
      }
    };
    
    connect();
    
    return () => {
      if (socket.current) {
        socket.current.close();
      }
    };
  }, [url]);

  return { data, error, connectionStatus, lastMessage };
}

// WebSocket connection setup - more flexible to handle any origin
const getWebSocketUrl = () => {
  const hostname = window.location.hostname;
  const wsProtocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';

  if (process.env.NODE_ENV === 'development') {
    return `ws://localhost:8002/ws`;
  }  
  
  if (hostname === 'localhost') {
    return `${wsProtocol}//localhost:8002/ws`;
  } else if (hostname.includes('pythonanywhere.com')) {
    return `${wsProtocol}//${hostname}/ws`;
  } else if (hostname.includes('masterfilmon.com')) {
    return `${wsProtocol}//api.masterfilmon.com/ws`;
  } else {
    // Fallback to current origin with ws endpoint
    return `${wsProtocol}//${window.location.host}/ws`;
  }
};

// Fetch historical data
async function fetchHistoricalData(metric, startTime, endTime, channels, apiBase) {
  console.log(`Fetching ${metric} data with time range:`, { startTime, endTime });
  
  try {
    const params = new URLSearchParams({
      start_time: startTime,
      end_time: endTime,
      channels: channels.join(',')
    });
    
    const url = `${apiBase}/api/data/${metric}?${params}`;
    console.log("Request URL:", url);
    
    const response = await fetch(url);
    console.log("Response status:", response.status);
    
    if (!response.ok) {
      throw new Error(`Network response was not ok: ${response.status} ${response.statusText}`);
    }
    
    const data = await response.json();
    console.log(`Data received with ${Object.keys(data).length} timestamps`);
    
    if (Object.keys(data).length > 0) {
      const sampleKey = Object.keys(data)[0];
      console.log("Sample data structure:", data[sampleKey]);
    }
    
    return data;
  } catch (error) {
    console.error("Error fetching historical data:", error);
    throw error;
  }
}

// Get icon for each tab
function getTabIcon(tab) {
  switch(tab) {
    case 'wavelength':
      return <Waves size={18} />;
    case 'power':
      return <Zap size={18} />;
    case 'strain':
      return <Activity size={18} />;
    case 'temperature':
      return <Thermometer size={18} />;
    default:
      return null;
  }
}

function Dashboard() {
  const [timeRange, setTimeRange] = useState(10); // Minutes
  const [selectedTab, setSelectedTab] = useState('wavelength');
  const [previousValues, setPreviousValues] = useState({});
  
  // Define the API base URL for all requests
  const API_BASE = getApiBaseUrl();
  
  // Setup WebSocket connection
  const wsUrl = getWebSocketUrl();
  console.log(`Using WebSocket URL: ${wsUrl}`);
  const { data: realtimeData, connectionStatus } = useWebSocket(wsUrl);
  console.log("WebSocket data received:", realtimeData);
  
  // Timezone-aware function for calculating time ranges
  const getLocalISOString = (date) => {
    // Get the ISO string but adjust for timezone offset
    const tzOffset = date.getTimezoneOffset() * 60000; // offset in milliseconds
    return new Date(date.getTime() - tzOffset).toISOString().replace('Z', '');
  };
  
  // Calculate time range for historical data using local time
  const endTime = getLocalISOString(new Date());
  const startTime = getLocalISOString(new Date(Date.now() - timeRange * 60 * 1000));
  
  console.log(`Using local time range: ${startTime} to ${endTime}`);
  
  // Fetch historical data
  const { 
    data: historicalData, 
    isLoading, 
    error: historyError 
  } = useQuery(
    ['historicalData', selectedTab, timeRange],
    () => fetchHistoricalData(selectedTab, startTime, endTime, [0, 1], API_BASE),
    {
      refetchInterval: 3000, // Refresh every 3 seconds
      retry: 3,
      onError: (error) => {
        console.error("Query error:", error);
      }
    }
  );

  // Process data for charts
  const chartData = React.useMemo(() => {
    if (!historicalData || Object.keys(historicalData).length === 0) {
      console.log('No historical data available');
      return [];
    }
    
    console.log('Processing', Object.keys(historicalData).length, 'historical data points');
    
    try {
      // Transform nested object into array format for Recharts
      const formattedData = [];
      
      // Process each timestamp
      for (const [timestamp, dataByChannel] of Object.entries(historicalData)) {
        const dataPoint = { timestamp };
        
        // Process data based on selected tab
        if (selectedTab === 'wavelength' || selectedTab === 'power') {
          // For channel/sensor structure
          for (const [channel, sensorData] of Object.entries(dataByChannel)) {
            for (const [sensor, value] of Object.entries(sensorData)) {
              const key = `CH${channel}S${sensor}`;
              dataPoint[key] = parseFloat(value);
            }
          }
        } else {
          // For flat sensor structure (strain, temperature)
          for (const [sensor, value] of Object.entries(dataByChannel)) {
            const key = `S${parseInt(sensor) + 1}`;
            dataPoint[key] = parseFloat(value);
          }
        }
        
        formattedData.push(dataPoint);
      }
      
      // Sort by timestamp
      formattedData.sort((a, b) => new Date(a.timestamp) - new Date(b.timestamp));
      
      console.log('Processed chart data, sample:', formattedData.slice(0, 2));
      return formattedData;
    } catch (error) {
      console.error('Error processing chart data:', error);
      return [];
    }
  }, [historicalData, selectedTab]);
  
  // Update previous values when new real-time data arrives
  useEffect(() => {
    console.log("Processing realtime data:", realtimeData);
    if (realtimeData) {
      setPreviousValues(prev => {
        const newValues = { ...prev };
        
        // Process wavelength data
        if (realtimeData.wavelengths) {
          realtimeData.wavelengths.forEach((channelData, channelIdx) => {
            channelData.forEach((value, sensorIdx) => {
              // Store raw values rather than formatted strings
              const key = `wavelength_CH${channelIdx}S${sensorIdx}`;
              newValues[key] = value; // Store as number directly
            });
          });
        }
        
        // Process power data
        if (realtimeData.powers) {
          realtimeData.powers.forEach((channelData, channelIdx) => {
            channelData.forEach((value, sensorIdx) => {
              const key = `power_CH${channelIdx}S${sensorIdx}`;
              newValues[key] = value; // Store as number directly
            });
          });
        }
        
        // Process strain data
        if (realtimeData.strains) {
          realtimeData.strains.forEach((value, sensorIdx) => {
            const key = `strain_S${sensorIdx + 1}`;
            newValues[key] = value; // Store as number directly
          });
        }
        
        // Process temperature data
        if (realtimeData.temperatures) {
          realtimeData.temperatures.forEach((value, sensorIdx) => {
            const key = `temperature_S${sensorIdx + 1}`;
            newValues[key] = value; // Store as number directly
          });
        }
        
        return newValues;
      });
    }
  }, [realtimeData]);

  // Safely format timestamp for display
  const formatTimestamp = (timestamp) => {
    if (!timestamp) return '';
    try {
      return new Date(timestamp).toLocaleTimeString();
    } catch (e) {
      console.error("Error formatting timestamp:", e);
      return String(timestamp);
    }
  };

  // Safely format full timestamp for tooltip
  const formatFullTimestamp = (timestamp) => {
    if (!timestamp) return '';
    try {
      return new Date(timestamp).toLocaleString();
    } catch (e) {
      console.error("Error formatting full timestamp:", e);
      return String(timestamp);
    }
  };

  return (
    <div className="min-h-screen flex flex-col py-4 px-3">
      <div className="bg-white rounded-lg shadow-md p-4 md:p-6 mb-6 flex-grow">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center">
            <Server className="text-indigo-600 mr-2" size={24} />
            <h1 className="text-xl md:text-2xl font-semibold">BraggMETER Monitoring Dashboard</h1>
          </div>
          
          <div className="flex items-center">
            <span className={`inline-flex h-3 w-3 rounded-full ${
              connectionStatus === 'connected' ? 'bg-green-500' :
              connectionStatus === 'connecting' ? 'bg-yellow-500' :
              'bg-red-500'
            } mr-2`}></span>
            <span className="text-sm text-gray-600">
              {connectionStatus === 'connected' ? 'Live' :
              connectionStatus === 'connecting' ? 'Connecting' :
              'Disconnected'}
            </span>
          </div>
        </div>
        
        {/* Tabs */}
        <div className="border-b border-gray-200 mb-4 md:mb-6 overflow-x-auto">
          <nav className="-mb-px flex">
            {['wavelength', 'power', 'strain', 'temperature'].map((tab) => (
              <button
                key={tab}
                className={`
                  ${selectedTab === tab
                    ? 'border-indigo-500 text-indigo-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'}
                  whitespace-nowrap py-3 px-3 border-b-2 font-medium text-sm mr-4 md:mr-8 flex items-center
                `}
                onClick={() => setSelectedTab(tab)}
              >
                <span className="mr-1.5">{getTabIcon(tab)}</span>
                {tab.charAt(0).toUpperCase() + tab.slice(1)}
              </button>
            ))}
          </nav>
        </div>
        
        {/* Time range selector */}
        <div className="mb-4 md:mb-6">
          <label htmlFor="time-range" className="block text-sm font-medium text-gray-700">
            Time Range
          </label>
          <select
            id="time-range"
            className="mt-1 block w-full md:w-64 pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm rounded-md"
            value={timeRange}
            onChange={(e) => setTimeRange(Number(e.target.value))}
          >
            {[1, 5, 10, 30, 60].map((minutes) => (
              <option key={minutes} value={minutes}>
                {minutes} {minutes === 1 ? 'minute' : 'minutes'}
              </option>
            ))}
          </select>
        </div>
        
        {/* Main content */}
        <div className="flex flex-col gap-4 md:gap-6">
          {/* Chart */}
          <div className="w-full">
            <div className="bg-white p-4 rounded-lg shadow-sm border border-gray-200 h-80">
              {isLoading ? (
                <div className="flex h-full items-center justify-center">
                  <p className="text-gray-500">Loading data...</p>
                </div>
              ) : chartData.length === 0 ? (
                <div className="flex h-full items-center justify-center">
                  <p className="text-gray-500">No data available</p>
                </div>
              ) : (
                <ResponsiveContainer width="100%" height="100%">
                <LineChart 
                    data={chartData}
                    margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
                >
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis 
                    dataKey="timestamp" 
                    tickFormatter={formatTimestamp}
                    />
                    <YAxis />
                    <Tooltip 
                    labelFormatter={formatFullTimestamp}
                    />
                    <Legend />
                    {chartData.length > 0 && 
                    (() => {
                        console.log("Chart keys:", Object.keys(chartData[0]).filter(k => k !== 'timestamp'));
                        return Object.keys(chartData[0])
                        .filter(key => key !== 'timestamp')
                        .map((key, index) => (
                            <Line 
                            key={key}
                            type="monotone"
                            dataKey={key}
                            name={key}
                            stroke={`hsl(${index * 30}, 70%, 50%)`}
                            dot={false}
                            />
                        ));
                    })()
                    }
                </LineChart>
                </ResponsiveContainer>
              )}
            </div>
          </div>
          
          {/* Sensor Cards */}
          <div className="w-full">
            <SensorCards 
              selectedTab={selectedTab}
              currentValues={realtimeData ? (
                selectedTab === 'wavelength' ? realtimeData.wavelengths : 
                selectedTab === 'power' ? realtimeData.powers : 
                selectedTab === 'strain' ? realtimeData.strains : 
                realtimeData.temperatures
              ) : null}
              previousValues={previousValues}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default Dashboard;